<template>
  <div class="contentRight">
    <cube-scroll-nav
      :side="true"
      :data="data"
      :current="current"
      @change="changeHandler"
      @sticky-change="stickyChangeHandler"
    >
      <ul class="prepend-header" slot="prepend"></ul>
      <cube-scroll-nav-panel
        v-for="item in data"
        :key="item.name"
        :label="item.name"
        :title="item.name"
      >
        <ul>
          <li v-for="food in item.foods" :key="food.id">
            <div class="book_content">
              <img :src="food.bookCover" class="book_left">
              <div class="book_right">
                <div class="book_right_tit">{{food.name}}</div>
                <div class="book_right_pri">{{food.bookPrice|money}}</div>
                <div class="book_right_descr">{{food.bookInfo}}</div>
                <div class="button" @click="collect(food)">
                  加入
                  <i class="cubeic-mall"></i>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </cube-scroll-nav-panel>
    </cube-scroll-nav>
    <cube-popup type="my-popup" :mask="false" ref="myPopup2">添加成功</cube-popup>
  </div>
</template>

<script>
import item from "./item";
import goodsData from "../../data/goods-list.json";
import CubePage from "../../components/cube-page";
const goods = goodsData.goods;

export default {
  data() {
    return {
      data: goods,
      current: goods[0].name
    };
  },
  components: {
    item,
    CubePage
  },
  computed: {
    arr() {
      return this.$store.state.listData;
    }
  },
  created() {
    if (!this.$store.state.isGetList) {
      this.$store.dispatch("getListData");
    }
 
  },
  methods: {
    showPopup(refId) {
      const component = this.$refs[refId];
      component.show();
      setTimeout(() => {
        component.hide();
      }, 600);
    },
    fn(options) {
      this.$store.commit("del", options);
    },
    changeHandler(label) {
      //console.log("changed to:", label);
    },
    stickyChangeHandler(current) {
      //console.log("sticky-change", current);
    },
    collect(options) {
      let obj = {
        bookCover: options.bookCover,

        bookId: options.bookId,

        bookInfo: options.bookInfo,

        bookName: options.name,

        bookPrice: options.bookPrice
      };
      this.showPopup("myPopup2");
      this.$store.commit("collect", obj);
    }
  }
};
</script>

<style lang="less" scoped>
.contentRight {
  height: 100%;
}
.book_content {
  display: flex;
  padding: 0.1rem 0.25rem;
  .book_left {
    width: 1.3rem;
    height: 2rem;
    margin-right: 0.2rem;
  }
  .book_right {
    flex: 1;
    padding-top: 0.3rem;
  }
  .book_right_tit {
    font-size: 0.3rem;
    float: left;
  }
  .book_right_pri {
    font-size: 0.5rem;
    float: right;
    color: #d46c75;
  }
  .book_right_descr {
    clear: both;
    font-size: 0.3rem;
    padding-top: 0.3rem;
    color: rgb(4, 7, 10);
    line-height: 20px;
  }
  .button {
    width: 1.2rem;
    height: 0.4rem;
    line-height: 0.4rem;
    font-size: 0.16rem;
    background: #14b9c8;
    color: white;
    border-radius: 3px;
    text-align: center;
    float: right;
    margin-top: 0.1rem;
    cursor: pointer;
  }
}
</style>